<template>
  <div class="monster-compass">
    <div v-if="step===1" class="compass-1-bg">
      <div class="compass-1-text">怪奇佈下結界來隱藏自己，使我們沒辦法知道他們的精確位置。結界都會有三個端點，只要你站在「定位點」上，正確擺放端點到羅盤上，取得破解結界的密碼，即可進入結界。博物館這邊會計算出三個端點，請速至地圖上閃爍的「定位點」待命。</div>
      <div @click="goTo2" class="compass-1-btn">已抵達</div>
    </div>
    <div v-if="step===2" class="compass-2-bg">
      <div class="compass-2-board">基隆郵便局</div>
      <div class="compass-2-board">基隆驛</div>
      <div class="compass-2-board">許梓桑厝</div>
      <div class="compass-2-text">博物館已經偵測出結界的三個端點。請站在『定位點』上，調整羅盤直到它正確呈現這三個地點的方位，破解結界的密碼就會顯現，即可進入結界。請拿出道具包的「怪奇羅盤」定位。</div>
      <div class="compass-2-green-text">請拿出道具包的「怪奇羅盤」定位。</div>
      <input v-model="code" class="compass-2-input" type="text">
      <div @click="go" class="compass-2-btn">GO</div>
    </div>
    <div v-if="step===3" class="compass-3-bg">
      <div class="compass-3-text-box">
        <div>一股令人毛骨悚然的氛圍從你身邊擴散開來，此時你才發現你身處在一棟陰森破舊的建築物旁，在繁華熱鬧的市中心裡顯得十分突兀。突然你感到背脊一陣寒意，聽到有人在呼喚你，你轉身後看到⋯⋯</div>
        <div>
          <div @click="goToHauntedHouse" class="compass-3-triangle"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'HauntedHouseCompass',
  components: {

  },
  data () {
    return {
      step: 1,
      code: ''
    }
  },
  mounted () {

  },
  destroyed () {

  },
  methods: {
    goTo2 () {
      this.step = 2
      this.$store.commit('changeHeader', false)
    },
    go () {
      if (this.code === 'XDN' || this.code === 'xdn') {
        this.step = 3
        alert('答對囉')
      } else {
        alert('答錯囉')
      }
    },
    goToHauntedHouse () {
      this.$router.push({
        path: './haunted-house'
      })
    }
  },
  computed: {

  }
}
</script>
<style lang="scss" scoped>

  .monster-compass {
    min-height: 100vh;
    padding-top: 100px;
    background: linear-gradient(#000 70%, #95ff00);
    position: relative;
    padding: 0px;

    .compass {

      &-1 {

        &-bg {
          // width: 100vw;
          height: 100vh;
          left: 0;
          top: 0;
          opacity: 0.8;
          display: flex;
          justify-content: center;
          align-items: center;
          flex-direction: column;
          background-image: url('../../assets/images/ling/ling-normal-map.png');
          background-size: cover;
          background-position-y: center;
          background-repeat: no-repeat;
          background-position-x: center;

        }

        &-text {
          color: white;
          padding: 20px;
          margin-top: 120px;
        }

        &-btn {
          width: 100%;
          height: 80px;
          line-height: 80px;
          color: white;
          text-align: center;
          background-color: darkgrey;
          margin-top: 10px;
        }
      }

      &-2 {

        &-bg {
          // width: 100vw;
          height: 100vh;
          left: 0;
          top: 0;
          opacity: 0.8;
          display: flex;
          justify-content: center;
          align-items: center;
          flex-direction: column;
          background-image: url('../../assets/images/ling/ling-green-map.png');
          background-size: cover;
          background-position-y: center;
          background-repeat: no-repeat;
          background-position-x: center;
          padding: 10px;
          margin: auto;
        }

        &-board {
          margin-top: 10px;
          width: 200px;
          height: 40px;
          line-height: 40px;
          text-align: center;
          color: black;

          &:first-child {
            margin-top: 20px;
          }
        }

        &-text {
          color: white;
          padding: 4px 10px;
          font-size: 14px;
        }

        &-green-text {
          color:#95ff00;
          padding: 10px;
        }

        &-input {
            width: 150px;
            padding: 4px;
            color: black;
        }

        &-btn {
          width: 50px;
          height: 50px;
          line-height: 50px;
          text-align: center;
          background: #95ff00;
          color: black;
          margin-top: 10px;
          border-radius: 100%;
        }
      }

      &-3 {

        &-bg {
          height: 100vh;
          left: 0;
          top: 0;
          opacity: 0.8;
          display: flex;
          justify-content: center;
          align-items: center;
          flex-direction: column;
          background-image: url('../../assets/images/ling/ling-blue-map.png');
          background-size: cover;
          background-position-y: center;
          background-repeat: no-repeat;
          background-position-x: center;
          padding: 10px;
          margin: auto;
        }

        &-text-box {
          min-height: 150px;
          padding: 20px;
          background-color: #95ff00;
          opacity: 0.8;
          margin-top: 160px;
          color: #000;
        }

        &-triangle {
          margin: 20px auto 0px;
          width: 0;
          height: 0;
          border-style: solid;
          border-width: 20px 20px 0 20px;
          border-color: black transparent transparent transparent;
        }
      }
    }
  }

</style>



